import React, { useCallback, useMemo, useRef, useState } from "react";
// third-pary
import { filter } from 'lodash';
import { DndProvider } from "react-dnd";
import { HTML5Backend } from "react-dnd-html5-backend";
// @mui
import { Box, Button, Stack, TextField, Typography } from "@mui/material";
import { AddRoad } from "@mui/icons-material";

// third-party
import { uniq, size } from 'lodash'
// project
import Column from "./Column";
import KanbanListToolbar from "components/kanban/KanbanListToolbar";

// assets
import { TODOS, COLUMNS } from "__mocks__/todos";
import MainCard from "components/cards/MainCard";
import PageContainer from "layouts/PageContainer";

function applySortFilter(array, query) {
    if (query?.trim().length > 0) {
        return filter(array, (_item) => _item.title?.toLowerCase().indexOf(query?.toLowerCase()) !== -1);
    }
    return array;
};

const Kanban = () => {
    const [columns, setColumns] = useState(COLUMNS);
    const [tasks, setTasks] = useState(TODOS);
    const [filterName, setFilterName] = useState('');
    const newColumn = useRef(null);

    const handleFilterByName = (event) => {
        setFilterName(event.target.value);
    };

    const filteredItems = useMemo(() =>
        applySortFilter(tasks, filterName)
        , [tasks, filterName])

    const handleDrop = useCallback((task, status) => {
        const updatedTasks = tasks.map((t) => {
            if (t.id === task.id) {
                return { ...task, status };
            }
            return t;
        });
        setTasks(updatedTasks);
        // eslint-disable-next-line
    }, [])

    const handleNewTask = useCallback((newTask) => {
        if (newTask?.title?.trim().length > 0) {
            setTasks([...tasks, newTask])
        }
        // eslint-disable-next-line
    }, []);

    const handleAddColumn = () => {
        const newCol = newColumn.current.value.trim();
        if (size(newCol) > 0) {
            setColumns(preColumns => uniq([...preColumns, newCol]))
        }
        newColumn.current.value = '';
    }

    return (
        <PageContainer>
            <Stack
                direction="row"
                alignItems="center"
                justifyContent="space-between"
                sx={{
                    mb: 2,
                    // bgcolor: theme.palette.mode === 'dark' ? 'grey.700' : 'grey.300',
                    // borderRadius: `${theme.shape.borderRadius}px`
                }}>
                <Typography variant="h3" gutterBottom>
                    Kanban
                </Typography>
                <Stack direction={'row'}>
                    <TextField
                        id="standard-basic"
                        inputRef={newColumn}
                        label="new column name"
                        variant="standard"
                        sx={{ width: 300 }} />
                    <Button
                        variant="contained"
                        startIcon={<AddRoad />}
                        onClick={handleAddColumn}
                    >
                        New Column
                    </Button>
                </Stack>
            </Stack>
            <MainCard>
                <KanbanListToolbar
                    filterName={filterName}
                    onFilterName={handleFilterByName}
                />
                <DndProvider backend={HTML5Backend}>
                    <Box
                        sx={{
                            display: 'flex',
                            flexWrap: 'wrap',
                            justifyContent: 'space-between',
                            alignItems: 'flex-start',
                            alignContent: 'flex-start'
                        }}
                    >
                        {columns?.map((column) => (
                            <Column
                                key={column}
                                status={column}
                                columns={columns}
                                editColumns={setColumns}
                                tasks={filteredItems}
                                editTasks={setTasks}
                                onDrop={handleDrop}
                                handleNewTask={handleNewTask} />
                        )
                        )}
                    </Box>
                </DndProvider>
            </MainCard>
        </PageContainer>
    );
};

export default Kanban;

